.btn-green {
  line-height: 1;
  border-radius: 500px;
  padding: 16px 48px 18px;
  background-color: #1DB954;
  border-color: #1DB954;
}

.btn-green:hover,
.btn-green:active,
.btn-green:focus {
  box-shadow: none!important;
  background-color: #1ED760!important;
  border-color: #1ED760!important;
}